.button {
  cursor: pointer;
  background-color: var(--color-red);
  padding: var(--p-lg) var(--p-xxl);
  border-radius: 8px;
  transition: all 0.3s ease;
  display: inline-block;
  min-width: 210px;
  text-align: center;
  color: var(--color-white);
}

button:hover {
  box-shadow: rgba(0, 0, 0, 0.4) 0 -4px 0 inset;
}

button:active {
  box-shadow: none;
  background-color: var(--color-red-active);
}