.noteWrapper {
  position: relative;
}

.pin {
  z-index: 1;
  position: absolute;
  right: 40%;
  top: -5px;
}

.note {
  cursor: grab;
  position: relative;
  width: 350px;
  height: 250px;
  padding: 20px 10px 10px 10px;
  background: #feea8f;
  font-family: 'Chelsea Market', cursive;
  font-size: 14px;
  line-height: 22px;
  overflow: hidden;
  color: var(--color-black);
  border-radius: 0 0 21px 0;

  box-shadow: var(--shadow);
}

.note::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 40px;
  background: linear-gradient(
    180deg,
    rgba(16, 16, 16, 0) 0%,
    rgba(254, 234, 143, 1) 100%
  );
}

.note:after {
  content: '';
  position: absolute;
  width: 18px;
  height: 18px;
  background: #dfc550;
  right: 0;
  bottom: 0;
  transform: rotate(4deg);
}